
.favorite {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: solid 3px rgb(253,235,103);
  border-radius: 15px;
  padding: clamp(.8em, 2vh, 3em) clamp(1em, 2vw, 2em);
  transition: all 0.2s ease-in-out;
  margin-bottom: 1em;
  width: clamp(15em, 60vw, 55em);
  min-width: fit-content;
}

.remove-fav-btn {
  font-size: clamp(.5em, .75em, 1.2em);
  font-weight: lighter;
  color: white;
  background: none;
  border: 1px solid white;
  border-radius: 10px;
  cursor: pointer;
  transition: ease-in .1s;
  margin-bottom: .5vh;
  height: 45%;
  padding: 0 1vw;
}

.remove-fav-btn:hover {
  color: #282c34;
  background: #e100a9;
  border: none;
  /* transform: scale(1.1, 1.1); */
}

.remove-fav-btn:focus {
  outline: none
}

.fav-details {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  padding: 0em clamp(1em, 2vw, 2em);
}

.fav-title-artist {
  flex: 2;
  line-height: 1.2em;
  color: white;
}

.fav-title-artist h1 {
  line-height: normal;
  margin: 0;
}

.fav-title-artist h3 {
  font-weight:300;
  margin: .25vh 0 .35vh 0;
  font-size: .8em;
}

.fav-date-genre {
  display: flex;
  font-size: .5em;
}

.fav-date-genre .fav-badge {
  border: solid 1.5px rgb(253,235,103);
  border-radius: 15px;
  padding: 0px 8px;
  background-color: white;
  color: rgb(40,44,52);
  font-size: .85vw;
  font-weight: 300;
  margin-right: 1em;
  padding: .25vw .75vw;
}

.spotify-button {
  margin-left: 2vw;
}

.fav-button-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex:.3;
}
